@import "../../styles/colors";
@import "../../styles/funcs";

.holder {
  padding-right: 50px;
  width: 100%;
  padding-top: 30px;

  .loader-sec {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;

  }

  .items {
    width: 70%;
    margin: 0 auto;
    padding-top: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;

    li {
      width: 47%;
      height: 180px;
      border-radius: 25px;
      background-size: cover;
      background-position: center;
      background-repeat: no-repeat;
      margin-bottom: 25px;

      &:nth-child(even) {
        margin-right: 3%;
      }

      &:nth-child(odd) {
        margin-left: 3%;
      }

      a {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.7);
        border-radius: 25px;
        flex-direction: column;

        &:hover {
          background-color: rgba(0, 0, 0, 0.3);

          i, ion-icon {
            @include fontSize(32);
          }

          .label {
            @include fontSize(22);
            margin-top: 15px;
          }
        }

        i, ion-icon {
          @include fontSize(45);
        }

        .label {
          @include fontSize(20);
          margin-top: 15px;
        }
      }
    }
  }
}