@import '../../../styles/funcs';
@import '../../../styles/colors';

.search-box {
  width: 100%;
  background-color: $bgLight;
  border-radius: 15px;
  padding: 20px;
  display: flex;
  align-items: center;

  .right {
    flex: 6;

    .datas {
      display: flex;
      align-items: center;
      justify-content: space-between;
      flex-wrap: wrap;
      padding-left: 25px;

      .form-group {
        min-width: 160px;
        margin-bottom: 20px;
      }
    }
  }

  .left {
    flex: 2;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: column;
    height: 100%;
  }
}

@media screen and (max-width: 800px) {
  .search-box {
    flex-direction: column;
    align-items: flex-start;

    .right {
      flex-direction: unset;
      width: 100%;

      .datas {
        flex-direction: column;
        align-items: flex-start;

        .form-group {
          width: 70%;

          &.double-slide {
            width: 100%;
          }

          input, select {
            width: 100%;
          }
        }
      }
    }

    .left {
      flex: unset;
      width: 100%;
      align-items: unset;

      .form-group {
        width: 70%;

        &.double-slide {
          width: 100%;
        }
      }
    }
  }
}