@import "../../styles/colors";
@import "../../styles/funcs";

.sidebar {
  flex: 1.5;
  height: calc(100% - 40px);
  border-radius: 25px;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  margin: 20px 25px 20px 0;

  .logo {
    height: 80px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 25px 25px 25px 0;
    background-color: $bgLight;

    .img {
      flex: 1;
      height: 100%;
      display: grid;
      place-items: center;

      h2 {

        text-align: center;
        @include fontSize(55);
        color: $redColor;
        font-weight: 600;
      }
    }

    .bookmark {
      display: grid;
      place-items: center;
      flex: 3;
      height: 100%;
      text-align: center;
      background-color: $bgColor;
      border-radius: 25px 0 50px 0;

      h2 {
        @include fontSize(22);
        color: $lightText;
      }
    }
  }

  .navigation {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    width: 100%;
    height: 100%;
    background-color: $bgLight;
    border-radius: 25px 0 25px 25px;

    .navs {
      display: flex;
      align-items: flex-start;
      justify-content: space-between;
      flex-direction: column;
      flex: 1;
      background-color: $bgColor;
      border-radius: 25px 0 0 0;
      padding-top: 30px;
      width: 100%;
      height: 100%;

      .list {
        display: flex;
        align-items: center;
        flex-direction: column;
        width: 100%;

        li {
          margin-bottom: 40px;
          position: relative;
          width: 100%;

          li:last-child {
            margin-bottom: 0;
          }

          &::before {
            content: '';
            position: absolute;
            width: 0;
            height: 0;
            left: -50%;
            top: -50%;
            transform: translate(50%, 0);
            border-radius: 50%;
            background-color: $bgLight;
            transition: all 0.3s;
          }

          &.active {

            &::before {
              width: 28px;
              height: 40px;
            }

            a {
              &:hover {
                &::after {
                  width: 0;
                }
              }

              svg {
                path {
                  fill: $redColor;
                }
              }

              &::after {
                width: 0;
              }
            }
          }

          a {
            display: flex;
            align-items: center;
            flex-direction: column;
            position: relative;

            svg {
              width: 24px;

              path {
                fill: $lightText;
              }
            }

            &::after {
              content: '';
              position: absolute;
              bottom: -7px;
              border-radius: 40px;
              height: 2px;
              width: 0;
              background-color: $lightText;
              transition: all 0.2s ease;
            }

            &:hover {
              &::after {
                width: 40%;
              }
            }
          }
        }
      }


      .top,
      .bottom {
        width: 100%;
      }
    }


    .sub-nav {
      flex: 3;
      width: 100%;
      height: 100%;
      border-radius: 25px 0 25px 25px;

      .lists-holder {
        overflow-y: auto;
        display: flex;
        align-items: flex-start;
        flex-direction: column;
        justify-content: space-between;
        width: 100%;
        height: 100%;
      }

      .headered-list {
        display: flex;
        width: 100%;
        height: 100%;
        align-items: flex-start;
        flex-direction: column;
        padding: 20px;
        margin-bottom: 20px;

        &:last-child {
          margin-bottom: 0;
        }

        .header {
          @include fontSize(18);
          color: $darkText;

        }

        .items {
          margin-top: 20px;
          display: flex;
          align-items: flex-start;
          flex-direction: column;
          padding-right: 10px;
          width: 100%;

          li {
            margin-bottom: 20px;
            width: 100%;
            height: 40px;

            a {
              display: flex;
              align-items: center;

              &:hover {
                transform: scale(1.02);

                i,
                span {
                  color: #fff;
                }

                svg {
                  path {
                    fill: #fff;
                  }
                }
              }
            }

            &:last-child {
              margin-bottom: 0;
            }

            i {
              margin-left: 10px;
              @include fontSize(13);
              color: $lightText;
            }

            svg {
              margin-left: 10px;
              width: 20px;
              path{
                fill:$lightText;
              }

            }

            span {
              @include fontSize(14);
              color: $lightText;
            }

            &.active {

              i,
              span {
                color: #fff;
              }
            }
          }
        }
      }
    }
  }
}

// Responsive
@media screen and (max-width: 800px) {
  .sidebar {
    display: none;
  }
}