@import "../../styles/funcs";
@import "../../styles/colors";

.small-movie {
  height: 100%;
  width: 100%;
  display: flex;
  align-items: flex-end;
  flex-direction: column;
  position: relative;

  &:hover {
    transform: scale(1.02);
  }

  .image {
    width: 100%;
    height: calc(100% - 60px);
    border-radius: 15px;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;

    &:hover {
      .overlay {
        opacity: 1;
      }
    }

    .overlay {
      position: absolute;
      background-color: rgba(0, 0, 0, 0.8);
      opacity: 0;
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;
      z-index: 5;
      border-radius: 15px;

      .story {
        height: 100%;
        width: 100%;
        padding: 10px;
        overflow: hidden;
        text-overflow: ellipsis;
        @include fontSize(12);
        color: $lightText;
      }
    }
  }

  .title {
    text-transform: capitalize;
    @include fontSize(14);
    width: 100%;
    text-align: left;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    margin-top: 15px;
  }

  .labels {
    display: flex;
    align-items: center;
    flex-direction: row-reverse;
    margin-top: 15px;

    .labeled-item {
      margin-left: 10px;

      &:first-child {
        margin-left: 0;
      }
    }
  }

  .additional {
    position: absolute;
    z-index: 4;
    right: 7px;
    top: 50%;
    transform: translateY(-80%);
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    width: 30px;

    & > div {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 30px;
      height: 30px;
      border-radius: 5px;
      transform: rotate(45deg);
      margin-bottom: 20px;

      i {
        color: #fff;
        transform: rotate(-45deg);
        @include fontSize(20);
      }
    }

    .subbed {
      background-color: $yellowColor;
    }

    .dubbed {
      background-color: $blueColor;
    }
  }
}