@import '../../../styles/colors.scss';
@import '../../../styles/funcs.scss';

.download-holder {
  height: 100vh;
  padding: 20px 0;

  .download-box {
    width: 100%;
    height: 100%;
    background-color: $bgLight;
    border-radius: 15px;
    overflow: auto;
    padding-bottom: 20px;

    .seasons {
      display: flex;
      align-items: flex-start;
      flex-direction: column;

      .season-holder {
        width: 100%;
        display: flex;
        align-items: center;
        flex-direction: column;

        .header {
          width: calc(100% - 50px);
          margin-top: 25px;
          padding: 20px;
          background-color: $bgColor;
          display: flex;
          align-items: center;
          justify-content: space-between;
          border-radius: 15px;

          &.active {
            .right {
              .header-label {
                color: $yellowColor;
              }

              i, ion-icon {
                color: $yellowColor;
                transform: rotate(90deg);
              }

              &:hover {
                .header-label, i, ion-icon {
                  color: $yellowColor;
                }
              }
            }
          }

          .right {
            display: flex;
            align-items: center;
            cursor: pointer;

            &:hover {
              .header-label, i, ion-icon {
                color: #fff;
              }
            }

            .header-label {
              color: $lightText;
              @include fontSize(18)
            }

            ion-icon, i {
              color: $lightText;
              transform: rotate(180deg);
              margin-right: 7px;
            }
          }

          .left {
            button {
              @include fontSize(12);
            }
          }
        }

        .episodes {
          width: 100%;
          height: 0;
          opacity: 0;
          overflow: auto;
          padding-left: 35px;
          padding-right: 35px;
          margin-left: 10px;

          &.active {
            height: 400px;
            opacity: 1;
          }
        }
      }
    }

    .links {
      display: flex;
      align-items: flex-start;
      flex-direction: column;
      padding: 20px;

      li {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        background-color: $bgColor;
        padding: 10px;
        border-radius: 15px;
        position: relative;
        margin-bottom: 25px;

        .right {
          display: flex;
          align-items: center;

          span {
            margin-right: 20px;
            color: $lightText;
            @include fontSize(14);

            &.label {
              color: $redColor;
            }

            &.subbed {
              color: $yellowColor;
            }

            &.dubbed {
              color: $blueColor;
            }
          }
        }
      }
    }
  }
}
