@import "../../styles/colors";
@import "../../styles/funcs";

.person-list {
  width: 100%;
  display: flex;
  align-items: flex-start;
  flex-direction: column;

  .header {
    color: $lightText;
    padding: 20px 40px;
  }

  .persons {
    display: flex;
    align-items: center;
    width: 100%;
    overflow: auto;

    li {
      width: 120px;
      margin-right: 20px;
      a{
        width: 100%;
        display: flex;
        align-items: center;
        flex-direction: column;
        &:hover{
          .name{
            color: #fff;
          }
        }
      }

      &:first-child {
        margin-right: 40px;
      }
      &:last-child {
        margin-left: 40px;
      }

      .image {
        width: 70px;
        height: 70px;
        border-radius: 50%;
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
      }

      .name {
        width: 100%;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        text-align: center;
        color: $lightText;
        @include fontSize(14);
        margin-top: 5px;
      }
    }
  }
}