@import "../../styles/colors";
@import "../../styles/funcs";
@import "../../styles/btns";

.posters {
  background-color: $bgLight;
  height: calc(100vh - 120px);
  min-height: 550px;
  width: 100%;
  border-radius: 25px;
  margin-top: 15px;
  padding: 25px;
  position: relative;

  .loader-sec {
    width: 100%;
    height: 100%;
    display: grid;
    place-items: center;
  }

  .poster-objects {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    border-radius: 25px;

    .img {
      display: none;
      background-size: cover;
      background-position: top;
      background-repeat: no-repeat;
      border-radius: 25px;
      width: 100%;
      height: 100%;

      &.active {
        display: block;
      }
    }
  }

  .details {
    width: 40%;
    height: 100%;
    backdrop-filter: blur(16px);
    background: rgba(255, 255, 255, 0.05);
    border-radius: 25px;
    padding: 20px;
    align-items: flex-start;
    flex-direction: column;
    display: none;

    &.active {
      display: flex;
    }

    .title {
      width: 100%;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      @include fontSize(20);
      text-align: center;

      &.persian {
        color: $darkText;
        text-align: right;
        @include fontSize(18);
        font-weight: 500;
        margin-top: 10px;
      }
    }

    .row {
      margin-top: 25px;

      & > span {
        color: $lightestText;
        @include fontSize(13);
        margin-left: 15px;

      }
    }

    .row {
      .labeled {
        display: flex;
        align-items: center;
        justify-content: space-between;
        border-radius: 40px;
        background-color: $bgLight;
        text-align: center;
        min-height: 30px;

        img {
          width: 30px;
          height: 30px;
          border-radius: 50%;
        }

        i {
          @include fontSize(20);
        }

        span {
          @include fontSize(12);
          color: #fff;
          text-align: center;
          width: 50px;
          margin: 0 !important;
        }

      }

      .genre-item {
        border-radius: 5px;
        padding: 5px 10px;
        background-color: $bgLight;
        text-align: center;
        @include fontSize(12);
        color: $lightText;

        &:hover {
          color: #fff;
        }
      }

      &.genres, &.ratings {
        flex-wrap: wrap;
        column-gap: 20px;
      }

      &.story {
        p {
          @include fontSize(12);
          color: $lightestText;
          font-weight: normal;
        }
      }

      &.funcs {
        display: flex;

        a {
          margin-left: 15px;
        }
      }
    }
  }

  .error {
    width: 100%;
    height: 100%;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    color: $redColor;
  }
}

// Responsive
@media screen and (max-width: 900px) {
  .posters {
    .details {
      width: 50%;
    }
  }
}

@media screen and (max-width: 750px) {
  .posters {
    .details {
      width: 60%;
    }
  }
}
@media screen and (max-width: 500px) {
  .posters {
    .details {
      width: 85%;
    }
  }
}