@import '../../../styles/colors.scss';
@import '../../../styles/funcs.scss';

.movie-content {
  overflow-y: hidden !important;

  .holder {
    padding-right: 50px;
    width: 100%;

    #details-holder {
      height: 100vh;
      margin-top: -80px;
      display: flex;
      align-items: center;
      justify-content: center;
    }

  }
}


// Responsive
@media screen and (max-width: 800px) {
  .movie-content {
    overflow-y: unset !important;

    .holder {
      padding-right: 0;
    }
  }
}