@import "../../styles/colors";
@import "../../styles/funcs";

.register-section {
  width: 100%;
  padding-right: 50px;
  padding-top: 20px;
  @import "../../components/Heading/heading";
  height: 100vh;
  overflow-y: hidden;

  .register-card {
    width: 100%;
    height: calc(100% - 100px);
    border-radius: 15px;
    background-color: $bgLight;
    position: relative;
    display: flex;
    align-items: center;
    flex-direction: column;

    .title {
      text-align: center;
      display: flex;
      align-items: center;
      flex-direction: column;
      position: absolute;
      z-index: 100;
      top: -34px;

      .icon {
        width: 70px;
        height: 70px;
        border-radius: 5px;
        transform: rotate(45deg);
        background-color: $bgLight;

        display: grid;
        place-items: center;

        i {
          transform: rotate(-45deg);
          @include fontSize(18);
          color: $yellowColor;
        }
      }

      h1 {
        margin-top: 10px;
        @include fontSize(20);
      }
    }

    .register-form {
      margin-top: 70px;
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      padding: 20px;
      flex-direction: column;

      .top {
        display: flex;
        justify-content: space-between;
        flex: 4;
        width: 100%;
        height: 50%;
        flex-wrap: wrap;

        .form-group {
          width: calc(50% - 20px);

          input[type="text"], input[type="email"], input[type="password"] {
            min-width: 100%;

            &:focus {
              border-color: $yellowColor;
              color: $yellowColor;
            }
          }
        }
      }

      .bottom {
        flex: 2;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        width: 50%;
        height: 50%;

        button {
          width: 100%;
          border-radius: 5px;
          justify-content: center;
        }

        .funcs {
          width: 100%;
          margin-top: 15px;
          display: flex;
          align-items: center;
          justify-content: space-between;

          a {
            @include fontSize(11);
            position: relative;
            color: $lightText;
            display: flex;

            &::after {
              content: '';
              position: absolute;
              right: 0;
              bottom: -7px;
              height: 1px;
              border-radius: 20px;
              background-color: #fff;
              width: 0;
              transition: all 0.3s ease;
              margin: 0 auto;
            }

            &:hover {
              color: #fff;

              &::after {
                width: 100%;
              }
            }
          }
        }
      }
    }

    .loading-page {

      position: absolute;
      width: 100%;
      height: 100%;
      right: 0;
      top: 0;
      display: flex;
      align-items: center;
      flex-direction: column;
      z-index: 120;
      background: rgba(255, 255, 255, 0.1);
      backdrop-filter: blur(7px);
      border-radius: 15px;

      .title {
        .icon {
          background: rgba(255, 255, 255, 0.1);
          backdrop-filter: blur(7px);
        }
      }

      .loader-sec {
        margin: auto;
      }
    }
  }

}

//Responsive
@media screen and (max-width: 800px) {
  .register-section {
    padding-right: 0;
    overflow-y: unset;
    padding-bottom: 30px;
    height: fit-content;


    .register-card {
      height: unset;
      min-height: calc(100vh - 100px);

      .register-form {
        .top {
          margin-bottom: 20px;
          flex-direction: column;

          .form-group {
            width: 100%;
            margin-bottom: 20px;
          }
        }
      }
    }
  }
}
