@import "../../../styles/colors";
@import "../../../styles/funcs";
.search-content{
  padding-top: 40px;
}
.search-holder {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  padding-right: 50px;

  .plain-text {
    @include fontSize(50);
    color: $darkenText;
    height: 70vh;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;

  }
}

// Responsive
@media screen and (max-width: 800px) {
  .search-holder{
    padding-right: 0;
  }
}