@import "../../styles/colors";
@import "../../styles/funcs";

.top-heading {
  width: 100%;
  display: flex;
  margin: 15px 0;
  align-items: center;
  justify-content: space-between;

  &.left {
    justify-content: flex-end !important;
  }

  .right {
    display: flex;
    align-items: center;

    i {
      margin-right: 10px;
      @include fontSize(25);
    }
  }

  .left {
    display: flex;
    align-items: center;

    &.fixed {
      position: fixed;
    }

    &.auto-fixed {
      position: fixed !important;
    }

    position: unset;
    left: 40px;
    z-index: 10;

    button:last-child {
      margin-right: 40px;
    }

    .search-form {
      width: 0;
      opacity: 0;
      height: 50px;
      background-color: $bgLight;
      margin-right: -25px;
      border-radius: 25px 0 0 25px;
      position: relative;

      &.active {
        width: 275px;
        opacity: 1;
      }

      input {
        width: 100%;
        height: 100%;
        border-radius: 25px;
        background-color: transparent;
        direction: ltr;
        @include fontSize(14);
        position: relative;
        padding: 0 25px 0 55px;
      }

      .clear-btn {
        position: absolute;
        left: 0;
        top: 0;
      }
    }
  }
}

@media screen and (max-width: 800px) {
  .top-heading {
    .left {
      left: 15px;
      top: 20px;
    }

  }
}