@import '../../../styles/colors.scss';
@import '../../../styles/funcs.scss';

.movie-content {
  overflow-y: hidden !important;

  .movie-holder {
    padding-right: 50px;
    width: 100%;

    #details-holder {
      height: 100vh;
      margin-top: -80px;
      display: flex;
      align-items: center;
    }

  }
}

//Responsive
@media screen and (max-width: 800px) {
  .movie-content {
    width: 100%;
    padding: 0 3%;
    margin-bottom: 0;
    overflow-y: unset !important;

    .movie-holder {
      width: 100%;
      padding: 0;
    }

  }
}