@import "../../../styles/funcs";
@import "../../../styles/colors";
.more-holder {
  height: 100vh;
  padding: 20px 0;

  .more-details {
    width: 100%;
    height: 100%;
    background-color: $bgLight;
    border-radius: 15px;
    overflow: auto;
    padding-bottom: 20px;

    .extras {
      width: 100%;
      display: flex;
      align-items: flex-start;
      justify-content: space-between;
      padding: 40px;
      padding-bottom: 20px;
      flex-wrap: wrap;

      li {
        width: 24%;
        overflow: hidden;
        text-overflow: ellipsis;
        display: flex;
        align-items: center;
        white-space: nowrap;
        margin-bottom: 25px;

        ion-icon, i {
          @include fontSize(18);
          color: $lightText;
          margin-left: 7px;
        }

        span {
          color: $lightText;

          &:last-child {
            margin-right: 7px;
          }
        }
      }
    }
  }
}

