@import "./styles/colors";
@import "./styles/funcs";

* {
  //border: 2px solid red;
}

.container {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: flex-start;

  .mobile-logo {
    margin: 0 auto;
    display: flex;
    margin-top: 20px;
    align-items: center;

    .img {
      h2 {
        font-weight: 500;
        color: $redColor;
        @include fontSize(40)
      }
    }

    .bookmark {
      margin-right: 10px;
      @include fontSize(40)
    }
  }
}

@media screen and (max-width: 800px) {
  .container {
    flex-direction: column;
    align-items: center;
  }
}

.content {
  flex: 5;
  height: 100%;
  overflow-y: auto;
  margin-left: 25px;
  padding-left: 15px;
  margin-bottom: 25px;
}

@media screen and (max-width: 800px) {
  .content {
    width: 100%;
    padding: 0 3%;
    margin-bottom: 0;
    overflow-y: unset !important;
    margin-left: 0;

    .home {
      padding-right: 0;
    }
  }
}