@import "../../styles/colors";
@import "../../styles/funcs";

.plans-section {
  width: 100%;
  padding-right: 50px;
  padding-top: 20px;
  @import "../../components/Heading/heading";
  height: 100vh;
  overflow-y: hidden;

  .plans-holder {
    width: 100%;
    height: calc(100% - 100px);
    position: relative;

    .plans {
      width: 100%;
      height: 100%;
      display: flex;
      align-items: flex-start;
      justify-content: space-between;

      li {
        width: calc(50% - 25px);
        background-color: $bgLight;
        border-radius: 15px;
        height: 100%;
        padding: 40px;
        display: flex;
        align-items: center;
        flex-direction: column;

        .icon {
          ion-icon {
            @include fontSize(55);
          }
        }

        .title {
          @include fontSize(24);
          margin: 20px 0;
        }

        .features {
          width: 100%;
          display: flex;
          align-items: flex-start;
          flex-direction: column;
          margin-top: 20px;

          .feature-item {
            margin-bottom: 15px;
            display: flex;
            align-items: center;

            &.green {
              span {
                color: $greenColor;

                i {
                  color: $greenColor;
                }
              }
            }

            &.red {
              span {
                color: $redColor;

                i {
                  color: $redColor;
                }
              }
            }

            span {
              margin-right: 7px;
              @include fontSize(14);
              color: $yellowColor;
              display: grid;
              place-items: center;

              &:first-child {
                margin-right: 0;
              }

              i {
                @include fontSize(14);
              }
            }
          }
        }

        .price {
          margin-top: 10px;

          span {
            margin-right: 5px;
            @include fontSize(12);
            color: $lightText;

            &:first-child {
              margin-right: 0;
              @include fontSize(35);
              color: $yellowColor;
            }
          }
        }

        .purchase-btn {
          margin-top: 20px;
          width: 100%;

          button {
            width: 100%;
          }
        }
      }
    }
  }

  .purchase-card {
    width: 100%;
    height: calc(100% - 100px);
    background-color: $bgLight;
    border-radius: 15px;
    padding: 40px;

    .info {
      h1 {
        @include fontSize(24);
        color: $yellowColor;
      }

      .items {
        margin-top: 30px;
        display: flex;
        align-items: flex-start;
        flex-direction: column;

        li {
          margin-bottom: 15px;

          .title {
            color: $lightText;
            @include fontSize(12);
          }

          .value {
            color: $yellowColor;
          }
        }
      }

      .funcs {
        margin-top: 30px;
        display: flex;
        align-items: center;

        button {
          border-radius: 5px;

          &:last-child {
            margin-right: 50px;
          }
        }
      }
    }
  }
}


//Responsive
@media screen and (max-width: 800px) {
  .plans-section {
    padding-right: 0;
    overflow-y: unset;
    padding-bottom: 30px;
    height: fit-content;

    .plans {
      flex-direction: column;

      li {
        width: 100% !important;
        margin-bottom: 30px;
      }
    }
  }
}