@import "../../styles/colors";
@import "../../styles/funcs";

.carousel-list {
  width: 100%;
  min-height: 550px;
  height: 120vh;
  margin-bottom: 30px;

  .header {
    display: flex;
    align-items: center;
    width: fit-content;
    height: 50px;

    .icon {
      padding: 0 15px;
      border-radius: 15px;
      display: grid;
      place-items: center;
      background-color: $bgColor;
      height: 50px;

      z-index: 3;

      i {
        @include fontSize(22);
      }

      ion-icon {
        @include fontSize(24);
      }
    }

    .title {
      padding: 0 20px;
      color: $lightText;
      @include fontSize(18);
      font-weight: 500;
      border-radius: 0 25px 0 0;
      background-color: $bgLight;
      display: flex;
      align-items: center;
      justify-content: center;
      height: 50px;
      position: relative;

      &::after {
        content: "";
        position: absolute;
        transform: translateX(-147%);
        width: 30px;
        height: 50%;
        right: -73%;
        bottom: 0;
        background-color: $bgLight;
        z-index: 0;
      }
    }

    .more {
      padding: 0 20px;
      display: flex;
      color: $darkText;
      height: 50px;
      @include fontSize(12);
      border-radius: 25px 0 0 0;
      background-color: $bgLight;
      align-items: center;
      justify-content: center;
      position: relative;

      &:hover {
        color: #fff;
      }

      &::after {
        content: "";
        position: absolute;
        width: 30px;
        height: 50%;
        left: -50%;
        bottom: 0;
        background-color: $bgLight;
        z-index: 1;
        transform: translate(50%, 0px);
      }

      &::before {
        content: "";
        position: absolute;
        width: 50px;
        height: 100%;
        left: -50%;
        bottom: 0;
        background-color: #111;
        z-index: 2;
        transform: translate(-13%, 0px);
        border-radius: 15px;

      }
    }

    .splitted {
      display: grid;
      place-items: center;
      height: 100%;
      background-color: $bgLight;
    }
  }

  .list {
    display: flex;
    align-items: flex-start;
    width: 100%;
    height: 100%;
    background-color: $bgLight;
    border-radius: 15px;

    .next-page, .prev-page {
      flex: 1;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: $bgLight;
      border-radius: 15px;

      &.disabled {
        cursor: not-allowed;

        &:hover {
          i {
            color: $lightText;
          }
        }
      }

      i {
        color: $lightText;
      }

      &:hover {
        i {
          color: #fff;
        }
      }
    }

    .items {
      flex: 15;
      height: 100%;
      width: calc(100% - 45px);
      display: flex;
      align-items: flex-start;
      justify-content: space-between;
      flex-wrap: wrap;
      padding: 20px 0;

      .error {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 100%;
        color: $redColor;
      }

      .loader-holder {
        width: 100%;
        height: 100%;
        display: grid;
        place-items: center;
      }

      li {
        width: calc(25% - 20px);
        height: calc(50% - 20px);
        margin-bottom: 20px;
      }
    }
  }
}

// Responsive
@media screen and (max-width: 650px) {
  .carousel-list {
    height: 170vh;

    .list {
      .next-page, .prev-page {
        height: 170vh;
      }

      .items {
        li {
          height: calc(33% - 20px);
          width: calc(33% - 20px);
        }
      }
    }
  }
}

@media screen and (max-width: 500px) {
  .carousel-list {
    height: fit-content;

    .list {
      .next-page, .prev-page {
        height: 3600px;
      }

      .items {
        li {
          height: 450px;
          width: calc(100%);
        }
      }
    }
  }
}
