@import "../../styles/colors";
@import "../../styles/funcs";

.mobile-menu {
  .more-btn {
    position: fixed;
    bottom: 25px;
    right: 25px;
    background-color: $redColor;
    border-radius: 5px;
    z-index: 100;
    width: 50px;
    height: 50px;
    display: grid;
    place-items: center;
    transition: all 0.3s ease;
    cursor: pointer;

    &.left-side {
      right: unset;
      left: 25px;
    }

    &.active {
      right: calc(100% - 75px);
    }

    ion-icon {
      @include fontSize(23);
      color: #fff;
    }
  }

  .navs {
    width: 60%;
    height: 100%;
    z-index: 67;
    position: fixed;
    display: none;
    background-color: $bgColor;
    right: 0;
    top: 0;

    &.active {
      display: unset;
    }

    .header {
      @include fontSize(24);
      color: $darkenText;
      text-align: center;
      padding: 15px 0;
    }

    .links {
      padding: 0 20px;
      display: flex;
      align-items: flex-start;
      margin-top: 20px;
      flex-direction: column;

      li {
        margin-bottom: 25px;
        width: 100%;

        &.active {
          .top {
            span {
              color: $redColor;
            }

            ion-icon, i {
              color: $redColor
            }
          }

          .sub-nav {
            height: 180px;
            opacity: 1;
          }

        }

        .top {
          display: flex;
          align-items: center;

          span {
            margin-right: 10px;
            color: $lightText;
            @include fontSize(14)
          }

          i, ion-icon {
            @include fontSize(20);
            color: $lightText;
          }
        }

        .sub-nav {
          height: 0;
          opacity: 0;
          overflow: hidden;

          .lists-holder {
            width: 100%;
            padding: 20px;
            padding-top: 25px;
            overflow-y: auto;

            .headered-list {
              .header {
                display: none;
              }

              .items {
                li {
                  margin-bottom: 15px;
                  display: flex;
                  align-items: center;

                  &.active {
                    a {
                      span, i, ion-icon {
                        color: $redColor;
                      }
                    }
                  }

                  a {
                    span {
                      @include fontSize(12);
                      color: $lightText;
                      margin-right: 10px;
                    }

                    i, ion-icon {
                      @include fontSize(12);
                      color: $lightText;
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}
