@import "./colors";
@import "./funcs";

.btn {
  padding: 10px 12px;
  border-radius: 15px;
  cursor: pointer;
  border: 2px solid transparent;
  display: flex;
  align-items: center;

  &.large {
    justify-content: center;
  }

  &:hover {

    i, ion-icon {
      transform: translateX(2px);
    }
  }

  i, ion-icon {
    margin-left: 10px;
  }

  span {
    @include fontSize(14);
    color: #fff !important;
  }

  i {
    @include fontSize(18);
  }

  &.primary-btn {
    background-color: $redColor;

    &:hover {
      background-color: transparent;
      border-color: $redColor;
    }
  }

  &.yellow-btn {
    background-color: $yellowColor;

    &:hover {
      background-color: transparent;
      border-color: $yellowColor;
    }
  }
  &.green-btn {
    background-color: $greenColor;

    &:hover {
      background-color: transparent;
      border-color: $greenColor;
    }
  }

  &.transparent {
    background-color: transparent;

    span {
      color: $darkText !important;
    }

    &:hover {
      border-color: #fff;

      span {
        color: #fff !important;
      }
    }
  }

  &.dark-btn {
    background-color: $bgLight;

    span {
      color: $lightText !important;
    }

    &:hover {
      border-color: $redColor;

      span {
        color: #fff !important;
      }
    }
  }
}