@import "../../styles/colors";
@import "../../styles/funcs";

.profile-content {
  overflow-y: hidden;
}

.profile-section {
  width: 100%;
  padding-right: 50px;
  padding-top: 20px;
  @import "../../components/Heading/heading";
  height: 100vh;
  //overflow-y: hidden;

  .profile-card {
    width: 100%;
    height: calc(100% - 100px);
    border-radius: 15px;
    background-color: $bgLight;
    position: relative;
    display: flex;
    align-items: center;
    flex-direction: column;

    .title {
      text-align: center;
      display: flex;
      align-items: center;
      flex-direction: column;
      position: absolute;
      z-index: 100;
      top: -34px;

      .icon {
        width: 70px;
        height: 70px;
        border-radius: 5px;
        transform: rotate(45deg);
        background-color: $bgLight;

        display: grid;
        place-items: center;

        i {
          transform: rotate(-45deg);
          @include fontSize(18);
          color: $yellowColor;
        }
      }

      h1 {
        margin-top: 10px;
        @include fontSize(20);
      }
    }

    .infos-form {
      margin-top: 60px;
      width: 100%;
      padding: 40px;
      display: flex;
      align-items: flex-start;
      flex-wrap: wrap;
      justify-content: space-between;

      .form-group {
        width: calc(50% - 20px);
        margin-bottom: 25px;

        input[type="text"], input[type="email"], input[type="password"] {
          min-width: 100%;

          &:focus {
            border-color: $yellowColor;
            color: $yellowColor;
          }
        }
      }

      .submit-btn {
        width: 100%;
        margin-top: 40px;

        button {
          border-radius: 10px;
          width: 100%
        }
      }
    }
  }
}

//Responsive
@media screen and (max-width: 800px) {
  .profile-content {
    overflow-y: unset;
  }
  .profile-section {
    padding-right: 0;
    overflow-y: unset;
    padding-bottom: 30px;
    height: fit-content;


    .profile-card {
      height: unset;
      min-height: calc(100vh - 100px);

      .infos-form {
        .form-group {
          width: 100%;
          margin-bottom: 20px;
        }

      }
    }
  }
}
