@import "./colors";
@import "./funcs";

.form-group {
  display: flex;
  align-items: flex-start;
  flex-direction: column;

  &.flat {
    flex-direction: row-reverse;
    align-items: center;
    justify-content: flex-end;

    label {
      margin-bottom: 0;
    }
  }

  label {
    @include fontSize(14);
    color: $lightestText;
    margin-bottom: 10px;
  }

  input[type="text"], input[type="email"], input[type="password"] {
    border-radius: 5px;
    background-color: $bgColor;
    padding: 8px 12px;
    @include fontSize(13);
    font-family: 'Poppins', sans-serif;
    color: #fff;
    border: 2px solid transparent;

    &:focus {
      color: $redColor;
      border-color: $redColor;
    }

    &.green-border {
      border-color: $greenColor !important;
      color: $greenColor;
    }

    &.red-border {
      border-color: $redColor !important;
      color: $redColor !important;

      &:focus {
        color: $redColor;
      }
    }
  }

  .help-text {
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    margin-top: 10px;

    & > div {
      display: flex;
      align-items: center;
      margin-bottom: 5px;

      span {
        @include fontSize(10);
        color: $lightText;
      }

      i {
        margin-right: 7px;
        @include fontSize(12);

        &.green {
          color: $greenColor;
        }

        &.red {
          color: $redColor
        }
      }
    }
  }

  .error-text {
    height: 16px;

    @include fontSize(10);
    color: $redColor;
    margin-top: 10px;
  }

  select {
    background-color: $bgColor;
    @include fontSize(13);
    color: #fff;
    border: 2px solid transparent;
    border-radius: 5px;
    padding: 8px 12px;

    &:focus {
      color: $redColor;
      border-color: $redColor;
    }

    option {
      @include fontSize(13);
      text-align: right;
      padding: 5px;
      margin-bottom: 10px;

      &:checked {
        background-color: $redColor;
        border-radius: 4px;
      }
    }
  }

  input[type="checkbox"] {
    display: none;
  }

  .checkLabel {
    width: 20px;
    height: 20px;
    border: 2px solid $lightText;
    border-radius: 4px;
    display: block;
    cursor: pointer;
    margin-left: 7px;

    &.checked {
      background-color: $redColor;
      border-color: $redColor;
      position: relative;
      transition: all .2s ease;

      &::after {
        transition: all .2s ease;
        position: absolute;
        content: "";
        @include fontSize(13);
        font-family: "Font Awesome 6 Pro", sans-serif;
        font-weight: 400;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
    }
  }

  &.double-slide {
    display: flex;
    flex-direction: column;
    width: 240px;
    align-items: flex-start;

    .top {
      display: flex;
      align-items: center;
      width: 100%;
      justify-content: space-between;

      p {
        color: $redColor;

        span {
          color: $redColor;
        }
      }
    }

    .bottom {
      width: 100%;

      .multi-range {
        position: relative;
        height: 50px;
        width: 100%;
        display: block;
        right: 0;
        bottom: 0;

        &::after {
          position: absolute;
          content: "";
          height: 4px;
          border-radius: 50px;
          width: 100%;
          background-color: #000;
          right: 0;
          top: 50%;
          z-index: 7;
          transform: translateY(-50%);
        }

        input[type=range] {
          position: absolute;
          width: 100%;
          height: 100%;
          appearance: none;
          background: transparent;
          cursor: pointer;
          pointer-events: none;
          z-index: 10;

          &::before {
            content: "";
            position: absolute;
            z-index: 10;
            background-color: $redColor;
            height: 4px;
            width: 0;
            top: 50%;
            transform: translateY(-50%);
          }

          &:first-child {
            left: 0;

            &::before {
              left: 0;
            }
          }

          &:last-child {
            right: 0;

            &::before {
              right: 0;
            }
          }

          &::-webkit-slider-runnable-track {
            background-color: transparent;
            -webkit-user-select: none;
            user-select: none;
            height: 10px;
            z-index: -1;
          }

          &::-moz-range-track {
            background-color: transparent;
            -moz-user-select: none;
            user-select: none;
            height: 10px;
            z-index: -1;
          }

          &::-webkit-slider-thumb {
            -webkit-appearance: none;
            /* Override default look */
            appearance: none;
            margin-top: -5px;
            /* Centers thumb on the track */
            background-color: $redColor;
            height: 20px;
            width: 20px;
            border-radius: 50%;
            pointer-events: all;
          }
        }
      }
    }
  }
}