@import "../../styles/colors";
@import "../../styles/funcs";

.player-holder {
  height: 100vh;
  width: 100%;
  padding: 40px 40px 20px 0;

  .small-btn {
    background-color: transparent;
    padding: 3px;
    display: grid;
    place-items: center;
    border-radius: 5px;

    &:hover {
      background-color: rgba(0, 0, 0, 0.7);

      i {
        color: #fff;
      }

      svg {
        path {
          fill: #fff;
        }
      }
    }

    i {
      @include fontSize(22);
      color: $lightText;
    }

    svg {
      width: 25px;

      path {
        fill: $lightText;
      }
    }

    i {
      @include fontSize(20);
    }
  }

  .change-collection {
    position: absolute;
    z-index: 6;
    top: 20px;
    right: 20px;
    background-color: $redColor;

    border-radius: 5px;

    .small-btn {
      &:hover {
        background-color: #da0c0c;
      }
    }

    svg {
      path {
        fill: #fff;
      }
    }

    i {
      color: #fff;
    }

    svg {
      width: 25px;
    }

    i {
      @include fontSize(25);
    }
  }

  .player-content {
    width: 100%;
    height: 100%;
    background-color: transparent;
  }

  .player {
    width: 100%;
    height: 100%;
    background-color: transparent;
    border-radius: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    position: relative;

    .pass-credit {
      position: absolute;
      bottom: 70px;
      right: 20px;
      z-index: 102;
      border-radius: 5px;
    }

    .overlay {
      width: 100%;
      height: 100%;
      position: absolute;
      background-color: rgba(0, 0, 0, 0.5);
      backdrop-filter: blur(3px);
      top: 0;
      left: 0;
      z-index: 3;
      opacity: 0;
      user-select: none;
      cursor: none;

      &.active {
        visibility: unset;
        opacity: 1;
        user-select: unset;
        cursor: unset;
      }

      .changeStat-btn {
        position: absolute;
        top: 50%;
        left: 50%;
        width: 70px;
        height: 70px;
        border-radius: 50%;
        z-index: 4;
        display: grid;
        place-items: center;
        background-color: transparent;
        transform: translate(-50%, -50%);

        &:hover {
          background-color: $bgColor;
        }

        svg {
          width: 30px;
          path{
            fill: #fff;
          }
        }

        i {
          @include fontSize(25);
        }
      }

      .bottom-funcs {
        position: absolute;
        width: 100%;
        height: 40px;
        bottom: 20px;
        left: 0;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 0 20px;

        .right {
          flex: 2;
          display: flex;
          align-items: center;
          justify-content: space-between;
        }

        .left {
          flex: 2;
          display: flex;
          align-items: center;
          justify-content: center;
          text-align: center;

          &.big {
            flex: 4;

            .control-volume {
              .indicator {
                width: 100px;
                margin-left: 5px;
                height: 5px;
                border-radius: 25px;
                background-color: $bgLight;

                input {
                  opacity: 1;
                  visibility: unset;
                  border-radius: 25px;
                }
              }
            }
          }

          span {
            color: $lightText;
            @include fontSize(14);
          }

          .splitter {
            margin: 0 5px;
            height: 14px;
            background-color: $lightText;
          }

          .control-volume {
            display: flex;
            align-items: center;
            margin-left: 5px;
            justify-content: center;

            .indicator {
              width: 0;
              position: relative;

              .passed {
                position: absolute;
                left: 0;
                top: 0;
                height: 100%;
                background-color: $redColor;
                border-radius: 25px;
                z-index: 3;
                transition: all 100ms ease;
              }

              input {
                opacity: 0;
                visibility: hidden;
                appearance: none;
                cursor: pointer;
                background-color: transparent;
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;

                &::-webkit-slider-thumb {
                  -webkit-appearance: none;
                  appearance: none;
                  width: 10px;
                  height: 10px;
                  border-radius: 50%;
                  background: $redColor;
                  cursor: pointer;
                }
              }
            }
          }
        }
      }
    }

    video {
      width: 100%;
      height: 100%;
      outline: none;
      border: none;
      position: relative;

      &::cue {
        font-family: "IRANSans", sans-serif;
        font-size: var(--subtitle-font-size) !important;
      }
    }

    .page {
      width: 100%;
      position: absolute;
      height: 100%;
      background-color: rgba(0, 0, 0, 0.7);
      z-index: 7;
      padding: 40px;
      right: 0;
      top: 0;
      display: none;

      .header {
        display: flex;
        align-items: center;

        span {
          margin-right: 5px;
          padding-bottom: 5px;
        }
      }

      .items {
        width: 100%;
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        margin-top: 50px;

        li {
          width: 15%;
          margin-bottom: 20px;
          background-color: $bgLight;
          border-radius: 5px;
          padding: 10px;
          display: flex;
          align-items: center;
          justify-content: center;
          margin-left: 20px;
          cursor: pointer;

          &.active,
          &:hover {
            background-color: $redColor;
          }
        }
      }

      &.settings-page {
        .items {
          flex-direction: column;
          align-items: flex-start;
          justify-content: unset;

          .item {
            width: 70%;
            margin-top: 20px;

            .label {
              @include fontSize(18);
              color: $lightText;
            }

            .func {
              width: 100%;
              margin-top: 20px;
              display: flex;
              align-items: center;
              justify-content: space-between;

              .right {
                flex: 2;

                .current {
                  color: $redColor;
                }
              }

              .left {
                flex: 6;

                .progress-indicator {
                  width: 100%;
                  // Inside .settings-page .progress-indicator
                  // input[type=range] {
                  //   &::-webkit-slider-thumb {
                  //     -webkit-appearance: none;
                  //     appearance: none;
                  //     width: 15px; // Size of the draggable circle
                  //     height: 15px;
                  //     background: $redColor; // Make it red to match your theme
                  //     border-radius: 50%; // Make it circular
                  //     cursor: pointer;
                  //   }
                  // }
                }
              }
            }
          }
        }
      }
    }
  }
}

// Responsive
@media screen and (max-width: 800px) {
  .player-holder {
    padding-right: 0;
  }
}