@import '../../../styles/colors.scss';
@import '../../../styles/funcs.scss';

.person-content {

  .person-movies-holder {
    padding-right: 50px;
    width: 100%;

    #details-holder {
      padding: 20px 0;
      height: 100vh;
    }

    .top-heading {
      margin-top: 30px;
    }

  }


}


// Responsive
@media screen and (max-width: 800px) {
  .person-content {
    .person-movies-holder {
      padding-right: 0 !important;

      .person-movies {
        margin-top: 40px;
      }
    }
  }
}